*{
    margin: 0px;
    padding: 0px;
    font-family: monospace;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

a,a:hover,a:focus{
    text-decoration: none;
    color: black;
}
@media(min-width:400px){
    *::-webkit-scrollbar {
        display:none;
    }
}
.logout{
    position: absolute;
    width: 100%;
   top: 0px;
   left: 0px;
   background-color: #222;
   border: 0px transparent;
   color: white;
   font-weight: 600;
   padding: 1em;

}
.loader{
    position: fixed;
    width: 100%;
    left: 0px;
    bottom: 0px;
    height: 100%;
    background-color: lightgreen;
    z-index: 100;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-weight: 900;
    font-size: 3em;
    color: #f56;
    text-shadow: 0px 0px 3px #f45;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    /* color: black; */
    overflow: hidden;
}

.hl{
    text-shadow: 0px 0px 3px #f45;
    padding: 0px;
font-size: 0px;
border-radius: 50%;
    color: transparent!important;
height: 0px;
width: 0px;
left:50%;
bottom:50%;
}
.hl .lds-ellipsis div{
    width: 0px;
    height: 0px;
    -webkit-box-shadow: 0px 0px 0px 0px #222;
            box-shadow: 0px 0px 0px 0px #222;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}



