.swap{
    position: absolute;
     width: 100%;
    bottom: 0px;
    background-color: #222;
    border: 0px transparent;
    color: white;
    font-weight: 600;
    padding: 1em;
}
.ani{
    -ms-flex: 1 1;
        flex: 1 1;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}
.show{
-ms-flex:5000 1;
    flex:5000 1;
width: 0px!important;
}
.hide{
    font-size: 0px;
    -ms-flex:1 1;
        flex:1 1;
    opacity: 0;
    width: 0px!important;
}
.full1{
    color: white;
    font-size: 3em;
    text-align: center;
    font-weight: 700;
    -ms-flex-pack: center;
        justify-content: center;
