.questionStatement{
    -ms-flex: 8 1;
        flex: 8 1;
     margin: 2em 5px;
         padding: 2em ;
         background-color: lightgoldenrodyellow;
         border: 0px transparent;
}
input[type=checkbox]{
    position: relative;
}

input[type=checkbox]:before{
    content: "";
    width: 20px;
    -webkit-box-shadow: 0px 0px 4px 2px #934;
            box-shadow: 0px 0px 4px 2px #934;
    border-radius: 50%;
    margin: 0px auto;
    height: 20px;
    top:-3px;
    left:-3px;
    position: absolute;
    background-color: #ddd;

}
input[type=checkbox]:checked:before{
background-color: lightgreen;
-webkit-box-shadow: 0px 0px 4px 2px green;
        box-shadow: 0px 0px 4px 2px green;

}






