.mainStyle{
    position: fixed;
    z-index: 2;
    background-color: rgba(32,32,32,1);
    color: white;
    text-align: center;
    overflow-y: scroll;
    padding: 0.5em;
                -webkit-box-shadow: 0em 0em 1em white;
                        box-shadow: 0em 0em 1em white;
                font-weight: 700;
                font-size: 1.5em;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-direction: row;
                    flex-direction: row;
                -ms-flex-align: center;
                    align-items: center;
                -ms-flex-pack: center;
                    justify-content: center;
                width: 100%;
                height: 100%;
}
.login{
    display: grid;
    grid-template-areas: 'x x x x x'
     'button1 button1 button1 button1 button1' 
     'label1 input1 input1 input1 input1' 
     'label2 input2 input2 input2 input2' 
     'button2 button2 button2 button2 button2';
     grid-gap: 1em;
     margin: auto;
}
.input1:active{
    -webkit-box-shadow: 0px 0px transparent;
            box-shadow: 0px 0px transparent;
    border-bottom: 1px solid #ff7;
}
.input1{
    -webkit-box-shadow: 0px 1px 0px #7f7;
            box-shadow: 0px 1px 0px #7f7;
}
.input{
    color: white;
    width: 100%;
    height: 30px;
    padding: 3px;
    border: 0px transparent;
    background-color:transparent;
    border-radius: 0.2em;
-webkit-transition: 0px;
-o-transition: 0px;
transition: 0px;
}

.button{
        grid-area:  button1 ;
        padding:  0.5em ;
        background-color:  #333 ;
        color:  white ;
        width:  100% ;
        margin:  auto ;
        border: 0px;
        border-radius: 0.2em;
        font-weight: 700;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
}
.button1{
    -webkit-box-shadow:1px 1px 1px 0px #f55;
            box-shadow:1px 1px 1px 0px #f55;

}
.button1:hover{
    background-color: #f77;

    -webkit-box-shadow: 0px 1px 2px #f34;

            box-shadow: 0px 1px 2px #f34;
}
.button2{
    grid-area: button2;
    -webkit-box-shadow:1px 1px 1px 0px #7f7;
            box-shadow:1px 1px 1px 0px #7f7;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
.button2:hover{
    -webkit-box-shadow:1px 2px 1px 0px #7f7;
            box-shadow:1px 2px 1px 0px #7f7;
}

.register{
    display: grid;
    grid-template-areas: 'x x x x x' 'button1 button1 button1 button1 button1' 'label1 input1 input1 input1 input1' 'label2 input2 input2 input2 input2' 'button2 button2 button2 button2 button2';
    grid-gap: 1em;
    margin: auto;
}
.button3{
    grid-area: button1;
    -webkit-box-shadow:1px 1px 1px 0px #7f7;
            box-shadow:1px 1px 1px 0px #7f7;
    -webkit-transition: 0.21s;
    -o-transition: 0.21s;
    transition: 0.21s;                 
}
.button3:hover{
    -webkit-box-shadow:1px 2px 1px 0px #7f7;
            box-shadow:1px 2px 1px 0px #7f7;
}
.input2{
    -webkit-box-shadow: 0px 1px 0px #f77;
            box-shadow: 0px 1px 0px #f77;
}
.input2:active{
    -webkit-box-shadow: 0px 0px transparent;
            box-shadow: 0px 0px transparent;
    border-bottom: 1px solid #f77;
}
.button4{
    grid-area: button2;
    -webkit-box-shadow:1px 1px 1px 0px #f77;
            box-shadow:1px 1px 1px 0px #f77;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
.button4:hover{
    -webkit-box-shadow:1px 2px 1px 0px #f77;
            box-shadow:1px 2px 1px 0px #f77;
    background-color: #f77;
}




